$border: rgb(109, 54, 54);
$hover: rgb(225, 225, 225);
$hover: rgb(225, 225, 225);
$border: rgb(109, 54, 54);
$header-bg: #343a40;
$light-gray: rgb(240, 240, 240);
$med-gray: rgb(200, 200, 200);
$med-blue: rgb(96, 120, 226);
$light-blue: rgb(126, 150, 255);
$signout-bg: #28a745;
$signout-bg-dark: #218639;

@mixin center-children {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.scenario-assignment-list {
    @include center-children();
    flex-direction: column;
    margin-top: 50px;
    
    h1 {
        font-size: 3em;
        margin-bottom: 25px;
    }

    .new-assignment-bttn {
        cursor: pointer;

        &:hover {
            background-color: $hover;
        }
    }

    .scenario-assignment-list-item {
        display: flex;
        border: 1px solid $border;
        border-radius: 5px;
        width: 50%;
        margin: 5px 0;
        height: 40px;
        padding: 10px;

        p {
            font-size: 1.5em;
            display: inline-block;
            margin-top: auto;
        }

        a {
            margin-left: auto !important;
        }

        .action-bttn {
            display: flex;
            background-color: $light-gray;
            padding: 5px 10px;
            border-radius: 3px;
            margin: 3px 10px;
            cursor: pointer;

            &:hover {
                background-color: $med-gray;
            }
        }
    }

    button {
        all: unset;
    }
}

.scenario-assignment {
    @include center-children();
    flex-direction: column;
    margin-top: 50px;

    * {
        label {
            display: flex;
            align-items: center;
        }

        input {
            padding: 3px;
        }
    }

    h1 {
        margin-top: 20px;
        font-size: 2.5em;
    }

    .assignment-title {
        margin-top: 20px;
        font-size: 1.5em;
        @include center-children();
        margin-bottom: 25px;

        input {
            width: 300px;
            margin-left: 15px;
        }
    }

    .assignment-list {
        display: flex;
        flex-direction: column;
        min-width: 20%;
        white-space: nowrap;

        label {
            font-size: 1.5em;

            input {
                width: unset;
                margin-right: 10px;
            }

            span {
                margin-left: auto;
                display: flex; 
                align-items: center;
                font-weight: 400 !important;
                padding: 0 20px;
                
                input {
                    width: 40px;
                    margin-left: 10px;
                }
            }
        }
    }

    button {
        margin-top: 30px;
        user-select: none;
        background-color: $light-blue;
        color: white;
        padding: 5px 10px;

        &:hover {
            background-color: $med-blue;
        }
    }

    .required-bttn {
        font-size: 1.2em;
        margin-top: 15px;
    }
}