$hover: rgb(225, 225, 225);
$border: rgb(109, 54, 54);
$header-bg: #343a40;
$light-gray: rgb(240, 240, 240);
$med-gray: rgb(200, 200, 200);
$signout-bg: #28a745;
$signout-bg-dark: #218639;

* {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', monospace;
    font-weight: lighter !important;
    text-decoration: none !important;
    color: black;
}

@media only screen and (max-width: 600px) {
    .list-scenario-item {
        h1 {
            max-width: 200px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        p {
            display: none;
        }
    }

    .new-scenario-bttn {
        p {
            display: block;
        }
    }
}

@mixin center-children {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

button {
    border: none;
    padding: 5px 10px;
    font-size: 1em;
    border-radius: 3px !important;
    margin-top: 10px;
    cursor: pointer;

    &:hover {
        background-color: $med-gray;
    }
}

.title-bar {
    position: sticky;
    top: 0;
    height: 75px;
    width: 100%;
    background-color: $header-bg;
    display: flex;  
    align-items: center;

    h1 {
        color: $light-gray;
        margin-left: 15px;
        font-weight: lighter !important;
    }
}

.index-page {
    display: flex;
    align-items: center;
    flex-direction: column;
    @include absolute-center;

    a {
        margin: 10px;

        .login-button {
            font-size: 2em;
            border: 1px solid $border;
            padding: 20px 60px;
        }

        div {
            padding: 5px 10px;
            border-radius: 5px;
            &:hover {
                background-color: $hover;
            }
        }
    }
}

.nav-wrapper {
    margin-top: 100px;
    @include center-children;
    flex-direction: column;
   
    a {
        margin: 10px;
        font-weight: lighter !important;
        text-decoration: none;
        color: black;
        
        .home-nav-button {
            @include center-children;
            border: solid 1px $border;
            height: 150px;
            width: 300px;
            border-radius: 10px;
            font-size: 1.5em;
    
            &:hover {
                background-color: $hover;
            }
        }
    }
}

.sign-out-bttn {
    margin: 0 15px 0 auto;
    border: 1px solid $signout-bg-dark;
    background-color: $signout-bg;
    color: white;

    &:hover {
        background-color: $signout-bg-dark;
    }
}

.back-bttn {
    position: absolute;
    top: 80px;
    left: 10px;
    border: 1px solid lightgray;
}

.scenario-list {
    @include center-children;
    flex-direction: column;
    margin-top: 15px;

    #scenario-list-title {
        margin-bottom: 10px;
    }

    .new-scenario-bttn {
        div {
            display: flex;
        }

        &:hover {
            background-color: $hover !important;
        }
    }

    .list-scenario-item {
        display: flex;
        border: 1px solid $border;
        border-radius: 5px;
        padding: 10px;
        margin: 5px 0;
        width: 85%;

        h1 {
            white-space: nowrap;
        }

        p {
            margin: 1em 0 0 10px !important;
            min-width: 60px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;

            &:first-of-type {
                font-style: italic;
            }

            &:not(:first-of-type) {
                width: 65%;
            }
        }

        .action-bttn {
            padding: 0 15px;
            height: 2.5em;
            margin: auto 10px;
            background-color: $light-gray;
            @include center-children;
            border-radius: 3px;
            border-color: $hover;
            white-space:nowrap;

            &:not(button) {
                margin-left: auto;
            }

            &:hover {
                background-color: $hover !important;
            }
        }
    }
}